(function () {
  'use strict';

  angular
    .module('myproperties')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController($timeout, toastr, $firebaseAuth, $log,$state) {
    var vm = this;
    var auth = $firebaseAuth();

    vm.classAnimation = '';
    vm.showToastr = showToastr;
    vm.signIn = signIn;

    activate();

    function signIn() {
      vm.firebaseUser = null;
      vm.error = null;
      auth.$signInWithEmailAndPassword(vm.formData.email, vm.formData.password).then(function (firebaseUser) {
        vm.firebaseUser = firebaseUser;
        $state.go("admin");
      }).catch(function (error) {
        vm.error = error;
        $log.log(error);
        showToastrWarning(error.message)
      });
    }

    function activate() {
      $timeout(function () {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function showToastrWarning(msg) {
      toastr.error(msg);
      vm.classAnimation = '';
    }


  }
})();

(function () {
  'use strict';

  angular
    .module('myproperties')
    .filter('fileExtension', fileExtension)

  function fileExtension() {

    return fileExtensionFn;

    function fileExtensionFn(filename) {
      return filename.split('.').pop();
    }
  }

}());

(function() {
    'use strict';

    angular
        .module('myproperties')
        .filter('clearObject', clearObject)

    function clearObject() {

        return clearObjectFn;

        function clearObjectFn(object) {
            object = angular.copy(object);
            var newObject = {};
            angular.forEach(object, function(value, index) {
                switch (index) {
                    case "$priority":
                        break;
                    case "$id":
                        break;
                    case "$$conf":
                        break;
                    default:
                        newObject[index] = value;
                        break;
                }

            });
            return newObject;
        }
    }

}());
(function () {
  'use strict';

  angular
    .module('myproperties')
    .run(runBlock);

  /** @ngInject */
  function runBlock($rootScope, $log, FB_CONFIG, firebase, $state) {
    firebase.initializeApp(FB_CONFIG);
    var auth = firebase.auth();
    $log.debug('runBlock end');

    $rootScope.$on("$stateChangeError", function (event, toState, toParams, fromState, fromParams, error) {
      // We can catch the error thrown when the $requireSignIn promise is rejected
      // and redirect the user back to the home page
      if (error === "AUTH_REQUIRED") {
        $state.go("login");
      }
    });

    auth.onAuthStateChanged(function (firebaseUser) {
      if (firebaseUser) {
        //console.log("Signed in as:", firebaseUser.uid);
      } else {
        $state.go("login");
      }
    });
  }



})();

(function () {
  'use strict';

  angular
    .module('myproperties')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider, $locationProvider, $translatePartialLoaderProvider, $translateProvider) {
    $translateProvider.useLoader('$translatePartialLoader', {
      urlTemplate: '{part}/i18n/{lang}.json'
    });
    $translateProvider.preferredLanguage('es');
    //$translateProvider.useSanitizeValueStrategy('sanitize');

    $translatePartialLoaderProvider.addPart('app');

    $locationProvider.html5Mode(true);
    $stateProvider
      .state('admin', {
        url: '/admin',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main',
        resolve: {
          "currentAuthF": ["AuthF", function (AuthF) {
            return AuthF.$requireSignIn();
          }]
        }
      })
      .state('admin.properties', {
        url: '/properties',
        views: {
          'content@admin': {
            templateUrl: function () {
              return 'app/main/views/propertiesRecords/propertiesRecords.html';
            },
            controller: 'PropertiesRecordsController',
            controllerAs: 'propertiesCtrl'
          }
        },
        resolve: {
          "currentAuthF": ["AuthF", function (AuthF) {
            return AuthF.$requireSignIn();
          }]
        }
      })
      .state('admin.properties.details', {
        url: '/details',
        params:{
          property:false
        },
        views: {
          'content@admin': {
            templateUrl: function () {
              return 'app/main/views/propertiesRecord/propertiesRecord.html';
            },
            controller: 'PropertiesRecordController',
            controllerAs: 'propertyCtrl'
          }
        },
        resolve: {
          "currentAuthF": ["AuthF", function (AuthF) {
            return AuthF.$requireSignIn();
          }]
        }
      })

      .state('login', {
        url: '/login',
        templateUrl: 'app/login/login.html',
        controller: 'LoginController',
        controllerAs: 'main'
      });
    $urlRouterProvider.otherwise('/admin/properties');
  }

})();

(function () {
  'use strict';

  angular
    .module('myproperties')
    .factory("AuthF", AuthF)
    .factory("AppF", AppF)
    .factory("FirebaseF", FirebaseF)
    .factory("FbStorage", FbStorage);

  function AuthF($firebaseAuth) {
    return $firebaseAuth();
  }

  function FbStorage(firebase) {
    return firebase.storage().ref();
  }

  function AppF($state, firebase) {
    var obj = {
      go: go,
      root: firebase.database().ref('/')
    };

    function go(state, params) {
      if (angular.isDefined(params) && typeof (params) == "object")
        $state.go(state, params);
      else
        $state.go(state);
    }
    return obj;
  }

  function FirebaseF($firebaseArray, AppF, firebase) {
    var obj = {
      loadList: loadList,
      prepareObject: prepareObject
    };

    function prepareObject(obj) {
      if (angular.isUndefined(obj.saved)) {
        obj.saved = {
          date: firebase.database.ServerValue.TIMESTAMP,
          user: firebase.auth().currentUser.uid
        }
      } else {
        obj.updated = {
          date: firebase.database.ServerValue.TIMESTAMP,
          user: firebase.auth().currentUser.uid
        }
      }
      return obj;
    }

    function loadNode(node) {
      if (angular.isUndefined(obj[node])) obj[node] = $firebaseArray(AppF.root.child(node));
    }

    function loadList(item) {
      switch (typeof (item)) {
        case "object":
          angular.forEach(item, function (ob) {
            loadNode(ob)
          });
          break;
        case "string":
          loadNode(item)
          break;
      }
    }

    return obj;
  }

})();

